.Navbar{
    background: #F05A22;
    height: 50px;
    display: flex;
    align-items: center;
    padding-left: 20px;;
}
.links{
    background-color: white;
    padding: 10px 20px 10px 20px;
    border-radius: 150px;
    margin-left: 30%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    gap: 40px
}
.links > a{
    color: #F9DDCF;
    text-decoration: none;
}
.links .active{
    color: #F05A22;
    border-bottom: 1px solid #F05A22;
}